import React from 'react';
import { useReducer } from "react";
import Header from './header';
import Footer from './footer';

const CartData = [
    {
      "id": 1,
      "productName": "Flower 1",  
      "productImage": "HB-250.jpg",
      "productPrice": "500",
      "count": 3,
    },
    {
      "id": 2,
      "productName": "Flower 2",    
      "productImage": "HB280.jpg",
      "productPrice": "900",
      "count": 5,
    },
    {
      "id": 3,
      "productName": "Flower 3",  
      "productImage": "S47-4553s__52710.1537976464.jpg",
      "productPrice": "750",
      "count": 7,
    },
  ]

  function reducer(state, action) {
    let id = action.id;
    switch (action.type) {
      case "increment":
        for(let i = 0; i <= CartData.length; i++) {
          if (id === CartData[i].id) {
            CartData[i].count = (CartData[i].count + 1);
            return { 
              ...state, 
              CartData: CartData
            };
          }
        }
      break;
      case "decrement":
        for(let i = 0; i <= CartData.length; i++) {
          if (id === CartData[i].id && CartData[i].count > 0) {
            console.log(CartData[i])
            CartData[i].count = CartData[i].count - 1;
            console.log(CartData[i])
            return { 
              ...state, 
              CartData: CartData
            };
          }
          // break;
        }
      break;
      case "reset":
        return CartData;
      default:
        throw new Error("Unexpected action");
    }
  }


function Cart() {
    const [state, dispatch] = useReducer(reducer, CartData);
  return (       
    <>
        <Header />  
        <div className="container">
            <h3 className="text-center p-3">Your Bag</h3>
            <div className="row">
            {CartData.map((item, index) =>
                <div  key={index} className="col-md-12 cart-bg mb-4">
                    <img  src={require(`../images/${item.productImage}`)} className="cart-img" alt={item.productImage} />
                    <div>
                        <h6>Product</h6>
                        <span>{item.productName}</span>
                    </div>
                    <div className="text-center">                      
                        <button  className="px-3"  onClick={() => dispatch({type: "decrement", id: item.id})} ><b>-</b></button>
                        <span> {item.count}</span>
                        <button  className="px-3" onClick={() => dispatch({type: "increment", id: item.id})} ><b>+</b></button>
                    </div>
                    <div className="text-center">                      
                        <h6>Rs.{item.productPrice}</h6>
                    </div>    
                    <div className="text-center">
                        <h6>Close</h6>                       
                    </div>                   
                </div>
                )}
                <a href="./home.js" ><h6 className="continue-link">Continue shoping</h6></a>
            </div>
         
        </div> 
        <Footer />
    </>
  );
}
export default Cart;

