import { useReducer } from "react";
import React from 'react';
import Header from './header';
import Footer from './footer';

const ProductData = [
  {
    "id": 1,
    "productName": "Flower 1",  
    "productImage": "HB-250.jpg",
    "productPrice": "500",
    "count": 0,
  },
  {
    "id": 2,
    "productName": "Flower 2",    
    "productImage": "HB280.jpg",
    "productPrice": "900",
    "count": 0,
  },
  {
    "id": 3,
    "productName": "Flower 3",  
    "productImage": "S47-4553s__52710.1537976464.jpg",
    "productPrice": "750",
    "count": 0,
  },
  {
    "id": 4,
    "productName": "Flower 4",  
    "productImage": "flower.jpg",
    "productPrice": "660",
    "count": 0,
  },
  {
    "id": 5,
    "productName": "Flower 5",   
    "productImage": "00666a410975c6454852dfeaad76a170.jpg",
    "productPrice": "500",
    "count": 0,
  },
  {
    "id": 6,
    "productName": "Flower 6",  
    "productImage": "blended-flower-bouquet-2_300.jpg",
    "productPrice": "400",
    "count": 0,
  },
  {
    "id": 7,
    "productName": "Flower 3",   
    "productImage": "Pink_Beauty_Bouquet_5565M_43389c70.jpg",
    "productPrice": "890",
    "count": 0,
  }
]

function reducer(state, action) {
  let id = action.id;
  switch (action.type) {
    case "increment":
      for(let i = 0; i <= ProductData.length; i++) {
        if (id === ProductData[i].id) {
          ProductData[i].count = (ProductData[i].count + 1);
          return { 
            ...state, 
            ProductData: ProductData
          };
        }
      }
    break;
    case "decrement":
      for(let i = 0; i <= ProductData.length; i++) {
        if (id === ProductData[i].id && ProductData[i].count > 0) {
          console.log(ProductData[i])
          ProductData[i].count = ProductData[i].count - 1;
          console.log(ProductData[i])
          return { 
            ...state, 
            ProductData: ProductData
          };
        }
        // break;
      }
    break;
    case "reset":
      return ProductData;
    default:
      throw new Error("Unexpected action");
  }
}


function Home() {
  const [state, dispatch] = useReducer(reducer, ProductData);
  return (
    <div className="App"> 
    <Header />     
     <div className="container">
        <h3 className="text-center p-3">Flower Store</h3>
        <div className="row">        
          {ProductData.map((item, index) =>
            <div key={index} className="col-md-4 product-card mb-5">
              <img src={require(`../images/${item.productImage}`)} className="img-size" alt={item.productImage} />
              <div className="product-detail">
                <div className="d-flex justify-content-between align-items-center p-2">
                  <span><b>{item.productName}</b></span>
                  <span>Price:&nbsp;<b>{item.productPrice}</b></span> 
                </div>
              </div>
              <div className="d-flex"> 
              <button className="m-0 px-3" onClick={() => dispatch({type: "decrement", id: item.id})}><b>-</b></button>
              <button className="add-cart-btn" >Add to cart : {item.count} </button>   
              <button className="m-0  px-3" onClick={() => dispatch({type: "increment", id: item.id})}><b>+</b></button>
              </div>

              {/* 
              <button onClick={() => dispatch({ type: "reset" })}>Reset</button>  */}
            </div>
          )}
        </div>
      </div> 
    <Footer />
    </div>
  );
}
export default Home;
